        <yd-flexbox v-for="item, key in list" :key="key" class="item">
             <div class="profile-wrapper">
                    <img  src="https://img.huxiucdn.com/auth/data/avatar/000/04/36/70_1481772895.jpg?imageView2/1/w/486/h/486/|imageMogr2/strip/interlace/1/quality/85/format/jpg">
                    <router-link to="/student/1/"><yd-button  bgcolor="#efefef" class="tagname" style="display:block;margin:0 auto">{{item.name}}</yd-button>
                    </router-link>
            </div>
            <yd-flexbox-item>
                <div class="desc-wrapper">
                <div class="title"> 
                    
                    <p> Major: {{item.major}}</p>
                    <span>Reputations :</span> 
