            },
                        {
                name:'Jenny',
                major:'Infomation technologies',
                school:'I need help for making my website. make sure looks awesome',
                skills:[
                    'CSS','Javescript','Python','C++','Java'],
                duration:'3',
                owner_profile:'https://img.huxiucdn.com/auth/data/avatar/000/04/36/70_1481772895.jpg?imageView2/1/w/486/h/486/|imageMogr2/strip/interlace/1/quality/85/format/jpg'
            },
          
          
           
        ]
      }

  }
}
</script>
<style lang="stylus" scoped>
.tag-wrapper
    .tags
        .item
            background #ffffff
            border-bottom 1px solid #efefef
            padding 10px
        width 100%
        display inline-block
        .profile-wrapper

            display inline-block
            position flex
            max-width 100px
            img
                width 50%
                border-radius 50%

        .desc-wrapper
            .yd-badge-hollow
                margin-right 10px
            display inline-block
            position flex
            width 100%
            padding 10px
            .title
                text-align left 
            .desc
                text-align left
                padding 10px 0
                .action
                    margin-top 10px
</style>



